import React, { useState, useEffect } from 'react';

// ÂõæÁâáÁºìÂ≠òÁÆ°ÁêÜ - ‰ΩøÁî®ÂÆâÂÖ®ÁöÑÊñπÂºèÂÆûÁé∞ÁºìÂ≠ò
const imageCache: Record<string, string> = {};
const preloadImage = (src: string): Promise<string> => {
  return new Promise((resolve, reject) => {
    if (imageCache[src]) {
      resolve(src);
      return;
    }

    const img = new Image();
    img.src = src;
    img.onload = () => {
      imageCache[src] = src;
      resolve(src);
    };
    img.onerror = reject;
  });
};

interface OptimizedImageProps {
  src: string;
  alt: string;
  className?: string;
  onClick?: () => void;
  loading?: 'lazy' | 'eager';
}

// ÁÆÄÂåñÁöÑÂç†‰ΩçÁ¨¶ - ‰ΩøÁî®ÂÆâÂÖ®ÁöÑÊñπÂºèÂàõÂª∫
const createPlaceholder = () => {
  // ‰ΩøÁî®ÂÜÖËÅîSVGËÄå‰∏çÊòØbase64ÁºñÁ†ÅÔºåÈÅøÂÖçbtoaÂÖºÂÆπÊÄßÈóÆÈ¢ò
  return `data:image/svg+xml,%3Csvg width="400" height="300" xmlns="http://www.w3.org/2000/svg"%3E%3Crect width="100%" height="100%" fill="%23f3f4f6"/%3E%3Ccircle cx="200" cy="150" r="30" fill="%23d1d5db" opacity="0.6"/%3E%3Crect x="185" y="135" width="30" height="30" fill="%239ca3af" opacity="0.8"/%3E%3C/svg%3E`;
};

const OptimizedImage: React.FC<OptimizedImageProps> = ({
  src,
  alt,
  className = '',
  onClick,
  loading = 'lazy'
}) => {
  const [isLoaded, setIsLoaded] = useState(!!imageCache[src]);
  const [hasError, setHasError] = useState(false);
  const [imageSrc, setImageSrc] = useState(imageCache[src] || src);

  useEffect(() => {
    let isMounted = true;

    const loadImage = async () => {
      if (!imageCache[src]) {
        try {
          await preloadImage(src);
          if (isMounted) {
            setImageSrc(src);
            setIsLoaded(true);
          }
        } catch (error) {
          if (isMounted) {
            setHasError(true);
          }
        }
      }
    };

    loadImage();

    return () => {
      isMounted = false;
    };
  }, [src]);

  const placeholder = createPlaceholder();

  return (
    <div className={`relative overflow-hidden bg-gray-100 ${className}`}>
      {/* Âç†‰ΩçÁ¨¶ */}
      {!isLoaded && !hasError && (
        <img
          src={placeholder}
          alt=""
          className="absolute inset-0 w-full h-full object-cover"
          aria-hidden="true"
        />
      )}
      
      {/* ÈîôËØØÁä∂ÊÄÅ */}
      {hasError && (
        <div className="absolute inset-0 bg-gradient-to-br from-gray-100 to-gray-200 flex items-center justify-center">
          <div className="text-gray-400 text-center">
            <svg className="w-12 h-12 mx-auto mb-2" fill="currentColor" viewBox="0 0 20 20">
              <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7 4a1 1 0 11-2 0 1 1 0 012 0zm-1-9a1 1 0 00-1 1v4a1 1 0 102 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
            </svg>
            <p className="text-xs">Image unavailable</p>
          </div>
        </div>
      )}
      
      {/* ÂÆûÈôÖÂõæÁâá */}
      {!hasError && (
        <img
          src={imageSrc}
          alt={alt}
          className={`w-full h-full object-cover transition-opacity duration-300 ${
            isLoaded ? 'opacity-100' : 'opacity-0'
          }`}
          loading={loading}
          onClick={onClick}
          onLoad={() => setIsLoaded(true)}
          onError={() => setHasError(true)}
        />
      )}
    </div>
  );
};

export default OptimizedImage;