const CACHE_VERSION = '2.0.3'; // Êõ¥Êñ∞Ëøô‰∏™ÁâàÊú¨Âè∑Êù•Âº∫Âà∂Âà∑Êñ∞ÊâÄÊúâÂõæÁâá

/**
 * ‰∏∫ÂõæÁâáURLÊ∑ªÂä†ÁâàÊú¨ÂèÇÊï∞Êù•ÈÅøÂÖçÁºìÂ≠òÈóÆÈ¢ò
 * @param imageUrl ÂéüÂßãÂõæÁâáURL
 * @returns Â∏¶ÁâàÊú¨ÂèÇÊï∞ÁöÑÂõæÁâáURL
 */
export const addVersionToImage = (imageUrl: string): string => {
  if (!imageUrl) return imageUrl;
  
  // Â¶ÇÊûúÂ∑≤ÁªèÊúâÊü•ËØ¢ÂèÇÊï∞ÔºåÊ∑ªÂä†&ÔºåÂê¶ÂàôÊ∑ªÂä†?
  const separator = imageUrl.includes('?') ? '&' : '?';
  return `${imageUrl}${separator}v=${CACHE_VERSION}`;
};

/**
 * Ëé∑ÂèñÂΩìÂâçÁºìÂ≠òÁâàÊú¨
 */
export const getCacheVersion = (): string => {
  return CACHE_VERSION;
};