// ‰ΩøÁî®Áé∞‰ª£ÂåñÁöÑgtag API

declare global {
  interface Window {
    dataLayer: any[];
    gtag: (...args: any[]) => void;
  }
}

/**
 * ÂèëÈÄÅÈ°µÈù¢ËÆøÈóÆÁªüËÆ°
 * @param page È°µÈù¢Ë∑ØÂæÑÔºåÈªòËÆ§‰∏∫ÂΩìÂâçË∑ØÂæÑ
 */
export const trackPageView = (page?: string) => {
  if (typeof window !== 'undefined' && window.gtag) {
    const pagePath = page || window.location.pathname;
    window.gtag('config', 'G-S8RQ2E295P', {
      page_path: pagePath,
    });
  }
};

/**
 * ÂèëÈÄÅËá™ÂÆö‰πâ‰∫ã‰ª∂ÁªüËÆ°
 * @param eventName ‰∫ã‰ª∂ÂêçÁß∞
 * @param parameters ‰∫ã‰ª∂ÂèÇÊï∞
 */
export const trackEvent = (eventName: string, parameters?: Record<string, any>) => {
  if (typeof window !== 'undefined' && window.gtag) {
    window.gtag('event', eventName, parameters);
  }
};

/**
 * ÂèëÈÄÅËÆæÂ§áÁÇπÂáªÁªüËÆ°
 * @param equipmentName ËÆæÂ§áÂêçÁß∞
 * @param category ËÆæÂ§áÂàÜÁ±ª
 */
export const trackEquipmentClick = (equipmentName: string, category: string) => {
  trackEvent('equipment_click', {
    equipment_name: equipmentName,
    equipment_category: category,
    event_category: 'engagement',
    event_label: `${category}-${equipmentName}`,
  });
};

/**
 * ÂèëÈÄÅÊêúÁ¥¢ÁªüËÆ°
 * @param searchQuery ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
 * @param resultCount ÊêúÁ¥¢ÁªìÊûúÊï∞Èáè
 */
export const trackSearch = (searchQuery: string, resultCount: number) => {
  trackEvent('search', {
    search_term: searchQuery,
    result_count: resultCount,
    event_category: 'engagement',
  });
};

/**
 * ÂèëÈÄÅÂàÜÁ±ªÁ≠õÈÄâÁªüËÆ°
 * @param category ÈÄâÊã©ÁöÑÂàÜÁ±ª
 */
export const trackCategoryFilter = (category: string) => {
  trackEvent('category_filter', {
    filter_category: category,
    event_category: 'navigation',
    event_label: category,
  });
};

/**
 * ÂèëÈÄÅÂõæÁâáÊü•ÁúãÁªüËÆ°
 * @param equipmentName ËÆæÂ§áÂêçÁß∞
 * @param category ËÆæÂ§áÂàÜÁ±ª
 */
export const trackImageView = (equipmentName: string, category: string) => {
  trackEvent('image_view', {
    equipment_name: equipmentName,
    equipment_category: category,
    event_category: 'engagement',
    event_label: `view_${equipmentName}`,
  });
};

/**
 * ÂèëÈÄÅÊäÄÊúØÂàÜÊûêÂ±ïÂºÄÁªüËÆ°
 * @param equipmentName ËÆæÂ§áÂêçÁß∞
 * @param expanded ÊòØÂê¶Â±ïÂºÄ
 */
export const trackTechnicalAnalysis = (equipmentName: string, expanded: boolean) => {
  trackEvent('technical_analysis', {
    equipment_name: equipmentName,
    action: expanded ? 'expand' : 'collapse',
    event_category: 'engagement',
  });
};