import { useEffect } from 'react';

declare global {
  interface Window {
    dataLayer: any[];
    gtag: (...args: any[]) => void;
  }
}

const GoogleAnalytics: React.FC = () => {
  useEffect(() => {
    // ÂàùÂßãÂåñGoogle Analytics
    const initGoogleAnalytics = () => {
      // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁªèÂä†ËΩΩËøáËÑöÊú¨
      if (document.querySelector('script[src*="googletagmanager.com/gtag/js"]')) {
        return;
      }

      // Google Analytics GA4 ‰ª£Á†Å - ÊåâÁÖßÂÆòÊñπË¶ÅÊ±Ç
      
      // 1. Âä†ËΩΩgtag.jsËÑöÊú¨
      const gtagScript = document.createElement('script');
      gtagScript.async = true;
      gtagScript.src = 'https://www.googletagmanager.com/gtag/js?id=G-S8RQ2E295P';
      document.head.appendChild(gtagScript);

      // 2. ÂàùÂßãÂåñdataLayerÂíågtagÂáΩÊï∞
      window.dataLayer = window.dataLayer || [];
      function gtag(...args: any[]) {
        window.dataLayer.push(args);
      }
      window.gtag = gtag;

      // 3. ÈÖçÁΩÆGA4
      gtag('js', new Date());
      gtag('config', 'G-S8RQ2E295P');
    };

    // È°µÈù¢Âä†ËΩΩÂÆåÊàêÂêéÂàùÂßãÂåñ
    if (document.readyState === 'loading') {
      document.addEventListener('DOMContentLoaded', initGoogleAnalytics);
    } else {
      initGoogleAnalytics();
    }

    // Ê∏ÖÁêÜ‰∫ã‰ª∂ÁõëÂê¨Âô®
    return () => {
      document.removeEventListener('DOMContentLoaded', initGoogleAnalytics);
    };
  }, []);

  // È°µÈù¢ËÆøÈóÆË∑üË∏™ - Áî®‰∫éSPAÈ°µÈù¢ÂàáÊç¢
  useEffect(() => {
    const trackPageView = () => {
      if (window.gtag) {
        // ÂèëÈÄÅÈ°µÈù¢ËÆøÈóÆÁªüËÆ°
        window.gtag('config', 'G-S8RQ2E295P', {
          page_path: window.location.pathname,
        });
      }
    };

    // Âª∂ËøüÊâßË°å‰ª•Á°Æ‰øùÁªüËÆ°ËÑöÊú¨Â∑≤Âä†ËΩΩ
    const timer = setTimeout(trackPageView, 1000);
    
    return () => clearTimeout(timer);
  }, []);

  // Ëøô‰∏™ÁªÑ‰ª∂‰∏çÊ∏≤Êüì‰ªª‰ΩïÂÜÖÂÆπ
  return null;
};

export default GoogleAnalytics;